@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    background-color: rgb(254, 248, 239);
}
.main{
    width: 100vw;
    display: flex;
    gap: 40px;
    padding: 40px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.card{
    height: 400px;
    width: 400px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
    background-color: white;
}
.card img{
    height: 50%;
    width: 95%;
    margin: auto;
    border-radius: 10px;
    background-color: white;
}
.card p, h2{
    padding: 0px 20px;
    background-color: white;
}
.card p{
    padding-bottom: 20px;
    color: rgb(130, 130, 130);
}
.category p{
    font-size: 14px;
    padding-bottom: 0px;
    font-weight: 700;
    color: rgb(130, 130, 130);
}
nav{
    width: 100vw;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn-group{
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.button{
    border: none;
    padding: 15px 30px;
    background-color: white;
    font-size: 14px;
    font-weight: 600;
}
.button:hover{
    background-color: blueviolet;
    border-radius: 10px;
}